html, body {margin:0;padding:0}
html, body, input, button {font-family:Arial, Helvetica, sans-serif;font-size:15px}

.register_holder {display:flex;height:100vh;align-items:center;justify-content:center;background-color:#EEF4F7}
    .register_pnl {width:min(400px,90vw);min-height:25vh;background-color:#ffffff;border-radius:8px}
        .register_pad {padding:24px}
            .register_ttl {font-size:2em;margin:0 0 16px;color:#4987AA}
            .register_row {background-color:#EEF4F7;padding:8px 12px 12px;border-radius:4px;margin:0 0 8px}

                .register_lbl {padding:0 0 4px;color:#4987AA}
                .register_row:nth-of-type(4) .register_lbl:nth-child(3) {padding:12px 0 4px}

                .register_ipt {position:relative}
                    .register_ipt input {position:relative;z-index:1;width:calc(100% - 26px);padding:0px 12px;height:40px;border:solid 1px #AFCAD9;transition:border 0.1s;background-color:#ffffff;outline:none}
                    .registeript input:hover {border:solid 1px #4987AA;box-shadow:0 0 3px rgba(175,202,217,1)}
                    .register_ipt_error input, .register_ipt_error input:hover {border:solid 1px #FF8400;box-shadow:0 0 3px rgba(255,132,0,0.5)}

                    .register_msg {position:absolute;top:0;transition:top 0.2s;right:0;display:flex;align-items:center;height:24px;font-size:13px;padding:1px 8px 0;border-radius:4px 4px 0 0;background-color:#FF8400;color:#ffffff}
                    .register_ipt_error .register_msg {top:-24px}

            .register_btn {padding:8px 0 0}
                .register_btn button {width:100%;border:0;height:40px;border-radius:4px;cursor:pointer;background-color:#21C939;color:#ffffff}
                .register_btn button:hover {background-color:#16D131}
                .register_btn_locked button {background-color:#efefef;color:#BDBDBD;pointer-events:none}

.toast_orange, .toast_red {position:fixed;bottom:5vh;right:-100%;transition:right 0.5s;background-color:#ffffff;padding:16px 20px;font-size:1.2em;border-radius:8px 0 0 8px;color:#ffffff;cursor:pointer}
.toast_open {right:0}
.toast_red {background-color:#ED0000}
.toast_orange {background-color:#FF8400}